import React, { useMemo, forwardRef } from 'react';
import size from 'lodash/size'

import RenderTable from './RenderTable'
import Box from '../components/Box'

const PageControl = forwardRef(({
  allData,
  headerRow,
  data,
  compareData,
  page,
  visible,
  sheetName,
  onCanvasLoaded,
}, ref) => {
  const compareMark = useMemo(() => {
    if (!size(compareData)) return null
    return compareData.reduce((all, row) => {
      all[row[1]] = row
      return all
    }, {})
  }, [compareData])

  const updatedMark = useMemo(() => {
    if (!compareMark) return null
    return data.map((row) => {
      const toCompare = compareMark[row[1]]
      if (toCompare) {
        return row.map((r, i) => r !== toCompare[i])
      } else {
        return row.map(() => true)
      }
    })
  }, [data, compareMark])
  if (!data.length) return null
  return (
    <Box
      pointerEvents={!visible && 'none'}
      position={!visible && 'absolute'}
      opacity={!visible && 0}
      height={!visible && 0}
      overflow={!visible && 'hidden'}
    >
      <RenderTable
        allData={allData}
        headerRow={headerRow}
        page={page}
        data={data}
        updatedMark={updatedMark}
        ref={ref}
        sheetName={sheetName}
        compareMode={size(compareData) > 0}
        onCanvasLoaded={onCanvasLoaded}
      />
    </Box>
  );
});

PageControl.defaultProps = {
  visible: true,
}

export default PageControl;
